<template>
  <div class="field pageSpace h100">
    <!-- <my-breadcrumb :extra="extraBreadcrumb"></my-breadcrumb> -->
    <!-- <div style="margin: 15px">频道： {{fieldName}} 字段管理</div> -->
    <!-- <div class="pageContent"> -->
      <a-layout-content>
        <div clss="field">
          <div class="tableTopTools clearfix">
            <div class="buttons fl">
              <p class="toolName fl">工具栏：</p>
              <a-button type="primary" @click="optfield">
                <a-icon type="plus"></a-icon>添加字段
              </a-button>
            </div>
          </div>
          <div class="tableContent">
            <a-table :columns="columns" :dataSource="fieldData" rowKey="id"
                     :pagination="{pageSize:50}">
              <div slot="num" slot-scope="text,record,index" class="clearfix">
                <div class="tableNum fl">{{index+1}}</div>
                <!-- <div class="fl" style="padding-left:15px" :class="{tableArrow:num+index+1 === 1||num+index+1 === total}">
                    <a class="disblock arrow" @click="adjust(record.id,fieldData[index-1].id,num+index,num+index-1)" title="上调" v-if="num+index+1 !== 1">
                        <a-icon type="caret-up"></a-icon>
                    </a>
                    <a class="disblock arrow" @click="adjust(record.id,fieldData[index+1].id,num+index,num+index+1)" title="下调" v-if="num+index+1 !== total">
                        <a-icon type="caret-down"></a-icon>
                    </a>
                </div> -->
              </div>
              <span slot="gmtCreate" slot-scope="text,record">
                <span>{{record.gmtCreate | dateFormatter(1)}}</span>
              </span>
              <span slot="gmtModified" slot-scope="text,record">
                <span>{{record.gmtModified | dateFormatter(1)}}</span>
              </span>
              <span slot="action" slot-scope="text,record">
                <a class="t-optColor-normal" @click="editfield(record)">编辑</a>
                <a class="t-optColor-warning" @click="deletefield(record)">删除</a>
              </span>
              <span slot="isEnabled" slot-scope="text,record">
                <a-switch size="small" @change="onSwitchChange(record)" v-model="record.isEnabled" />
              </span>
            </a-table>
            <!--<div class="ar" style="margin-top:30px" v-if="fieldData.length > 0">
              <a-pagination
                v-model="pageParam.page"
                :total="total"
                :page-size="pageParam.size"
                @change="pageChange"
              ></a-pagination>
            </div>-->
          </div>
        </div>
      </a-layout-content>
    <!-- </div> -->
  </div>
</template>
<script>
export default {
  name: "project-list",
  // components: {fieldModal},
  props:{
    fieldData: {
      type:Array,
      default(){
        return []
      }
    },
    currentNode: {},
  },
  data() {
    return {
      // 表格列
      columns: [
        {
          dataIndex: "num",
          title: "序号",
          scopedSlots: { customRender: "num" },
        },
        {
          dataIndex: "cname",
          title: "字段名称",
        },
        {
          dataIndex: "ename",
          title: "字段英文名",
        },
        {
          dataIndex: "type",
          title: "字段数据类型",
        },
        {
          dataIndex: "creator",
          title: "创建者",
        },
        {
          dataIndex: "gmtCreate",
          title: "创建时间",
          scopedSlots: { customRender: "gmtCreate" },
        },
        {
          dataIndex: "gmtModified",
          title: "最近更新时间",
          scopedSlots: { customRender: "gmtModified" },
        },
        // {
        //   dataIndex: "isEnabled",
        //   title: "是否启用",
        //   scopedSlots: { customRender: "isEnabled" },
        // },
        {
          dataIndex: "action",
          title: "操作",
          scopedSlots: { customRender: "action" },
        },
      ],
      // 表格数据
      // fieldData: [],
      pageParam: {
        page: 1,
        size: 500,
        channelId: this.$route.query.id,
      },
        customPage:{
          page:1,
          size:10
        },
      searchInput: "",
      total: 0,
      extraBreadcrumb: ["频道字段管理", this.$route.query.cname],
    };
  },
  computed:{
      num(){
          return (this.pageParam.page-1)*this.pageParam.size;
      }
  },
  methods: {
    /***
     *  调整字段顺序
     * @param {sid} 点击行的id
     * @param {tid} 目标id
     * @param {snum} 点击行的index+1 因为数组index是0开头 我们存储findex要1开头
     * @param {tnum} 目标行的index+1
     */
    adjust(sid,tid,snum,tnum){
        console.log(sid)
        console.log(snum)
        console.log(tid)
        console.log(tnum)
        // 做处理 snum变为tid数据的findex  tnum变为sid数据的index
        let arr = [
            {
                id:sid,
                findex:tnum
            },
            {
                id:tid,
                findex:snum
            }
        ];
        console.log(arr)
        let params ={
            list:arr
        };
        // 走批量更新接口  仅仅更新findex
        this.$API.bathUpdate(params).then((res) => {
            if (res.code === 1000) {
                this.getfield()
            } else {
                this.$message.error("调整字段顺序错误")
            }
        });
    },
    //  查询字段
    getfield() {
        this.$API.searchField(this.pageParam).then((res) => {
        if (res.code === 1000) {
          for (let item of res.data) {
            item.isEnabled = item.isEnabled === 1 ? true : false;
          }
          this.fieldData = res.data;
            this.total = res.total;
        } else {
          this.fieldData = [];
        }
      });
    },
    // 添加
    optfield() {
      console.log(this.fieldData)
      console.log(this.currentNode)
      this.$router.push({
        path: `/business/setting/field/add`,
        query: {
          cid: this.currentNode.id,
          ctype: this.currentNode.ownerType,
        },
      });
    },
    //   编辑
    editfield(column) {
      this.$router.push({
        path: `/business/setting/field/edit`,
        query: {
          channelId: this.$route.query.id,
          fieldId: column.id,
          channelName: this.$route.query.cname,
        },
      });
    },
    // 删除
    deletefield(column) {
      const that = this;
      this.$confirm({
        title: "确定删除该条数据吗？",
        content: () => <div style="color:red">删除后将不可恢复</div>,
        okText: "确认删除",
        cancelText: "取消删除",
        onOk() {
          that.$API.delField(column.id).then((res) => {
            if (res.code === 1000) {
              that.$message.success("删除成功");
                that.$emit('refresh',true)
            } else {
              that.$message.warning(res.message);
            }
          });
        },
      });
    },
    pageChange() {
      this.getfield();
    },
    // 搜索关键词变化事件
    keywordChange() {
      this.pageParam.page = 1;
      this.getfield();
    },
    onSwitchChange(row) {
      let obj = { id: row.id, isEnabled: row.isEnabled ? 1 : 0 };
      this.$API.editField(obj).then((res) => {
        if (res.code === 1000) {
          this.$message.success("字段更新成功");
          this.fieldVisible = false;
          this.getfield();
        } else {
          this.$message.warning(res.message);
        }
      });
    },
  },
  created() {
    // this.getfield();
  },
};
</script>
<style>
.searchInput {
  width: 300px;
}
.arrow i{
    font-size: 16px;
}
.arrow:hover {
    color:#fff;
}
</style>